import * as React from "react";
import axios from "axios";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Modal from "@mui/material/Modal";
import "./modal.css";
import { Autocomplete, TextField } from "@mui/material";
import { getCities } from "../../../Services/CityNames";
import { ADD_NEW_EVENT } from "../../../utils/Events/Constants/constants";


export default function AddNew() {
  // Cities API handlers
  const [cities, setCities] = React.useState([]);
  let temp_arr = [];

  // Get current User
  const user_email = localStorage.getItem("email");

  // Drop down options handlers
  let options = [];
  for (let i = 9; i <= 20.5; i += 0.5) {
    options.push(i);
  }

  // Modal handlers
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  // New event handler
  const [name, setName] = React.useState("");
  const [location, setLocation] = React.useState("");
  const [allDay, setAllDay] = React.useState((a) => true);
  const [start, setStart] = React.useState((a) => "9");
  const [end, setEnd] = React.useState((a) => "9.5");
  const [error, setError] = React.useState("");

  // Adding New event in the database
  const AddNewEvent = (a) => {
    a.preventDefault();

      axios
        .post(ADD_NEW_EVENT, {
          start,
          end,
          name,
          location,
          allDay,
          user_email,
        })
        .then((res) => {
          if (!res.ok) {
            setError("Could not save data. Make sure all fields are full");
            handleOpen();
          }
        });
    }

  // Handling disabling options in end time list that are smaller than start time
  const disable = (e) => {
    var id = "";
    document.querySelectorAll(".start").forEach((opt) => {
      if (opt.value === e.target.value) {
        id = opt.id;
        setStart(id);
      }
    });
    document.querySelectorAll(".end").forEach((opt) => {
      if (parseFloat(opt.id) <= parseFloat(id)) {
        opt.disabled = true;
      }
    });
  };

  // For removing duplicates in the cities API
  function removeDuplicates(arr) {
    var unique = [];
    arr.forEach((element) => {
      if (!unique.includes(element)) {
        unique.push(element);
      }
    });
    return unique;  
  }

  // Get all the cities of Pakistan from this function
  const getLocations = async () => {
    await getCities(temp_arr); // This function usees third party aoi to fetch cities
    setCities(removeDuplicates(temp_arr)); // Setting Cities state
  };

  React.useEffect(() => {
    getLocations();
  }, []);

  return (
    <div>
      <Button className="blue-btn" title="Add a new event" onClick={handleOpen}>
        <span className="blue-txt">Add New</span>
      </Button>
      <Modal open={open} onClose={handleClose}>
        <Box className="modal-box">
          <form className="modal-form">
            <h1 className="all">Add a New Event</h1>
            <label>Event Name</label>

            <input
              type="text"
              name="name"
              onChange={(e) => setName(e.target.value)}
              required
            />
            <Autocomplete
              required
              disablePortal
              id="combo-box-demo"
              options={cities && cities}
              sx={{ width: "100%", height: "50px" }}
              onChange={(e) => setLocation(e.target.textContent)}
              renderInput={(params) => (
                <TextField {...params} label="Location" />
              )}
            />
            <label>Event Type:</label>
            <div className="radio">
              <input
                className="radio-btn"
                type="radio"
                id="allDay"
                required
                name="event_type"
                value={true}
                onChange={(e) => setAllDay((a) => e.target.value)}
              />
              All Day
              <input
                className="radio-btn"
                type="radio"
                id="timely"
                required
                name="event_type"
                value={false}
                onChange={(e) => setAllDay((a) => !allDay)}
              />
              Timely
            </div>
            {allDay == false ? (
              <div className="time-fields">
                <label>Start Time</label>
                <select
                  required
                  value={start}
                  onChange={(e) => {
                    setStart((a) => e.target.value);
                    disable(e);
                  }}
                >
                  {options &&
                    options.map(
                      (
                        opt // Renders a list of times in dropdown
                      ) => (
                        <option
                          className="start"
                          id={opt}
                          key={options.indexOf(opt)}
                          value={opt}
                        >
                          {opt}
                        </option>
                      )
                    )}
                </select>
                <label>End Time</label>
                <select
                  required
                  value={end}
                  onChange={(e) => {
                    setEnd(e.target.value);
                  }}
                >
                  {options &&
                    options.map(
                      (
                        opt // Renders a list of times in dropdown
                      ) => (
                        <option
                          className="end"
                          id={opt}
                          key={options.indexOf(opt)}
                          value={opt}
                        >
                          {opt}
                        </option>
                      )
                    )}
                </select>
              </div>
            ) : (
              <></>
            )}
            <div className="error">{error}</div>
            <div className="btns">
              <button className="cancel" onClick={handleClose}>
                Cancel
              </button>
              <button  onClick={AddNewEvent}>
                Add
              </button>
            </div>
          </form>
        </Box>
      </Modal>
    </div>
  );
}
