import React, { useState } from "react";
import useFetch from "../../Hooks/useFetch";
import "./calendar.css";
import { GET_EVENTS } from "../../utils/Events/Constants/constants";
import { am, pm } from "../../utils/Events/Constants/constants";
import { outLocal } from "../../utils/Users/HelperFunctions";
import AllDayEvent from "../../Components/Events/AllDayEvents/allDay";
import {
  timeFormatHandler,
  divide,
  collision,
  render,
} from "../../utils/Events/HelperFunctions/calendarFunctions";

export default function Calendar() {
  const user_email = outLocal("email");

  let TimeEvent = [];
  let all_Day = [];

  // Get all events
  const { data, error } = useFetch(GET_EVENTS(user_email));

  // function calls
  if (data) {
    divide(data, all_Day, TimeEvent);
    render(TimeEvent);
    collision(TimeEvent);
  }

  return (
    <div className="container">
      <div className="main" id="main-div">
        <div className="header">
          <div className="date">Friday,April 1</div>
        </div>
        <div className="all-day" id="8">
          {all_Day &&
            all_Day.map((event, index) => {
              return (
                <AllDayEvent
                  key={index}
                  id={`All-day-${index}`}
                  name={event.name}
                  location={event.location}
                />
              );
            })}
        </div>
        <div className="am">
          <div className="heading-am">
            <h1>AM</h1>
          </div>
          <div className="time-divs-am">
            {am.map((time, index) => {
              if (!time.includes(".5")) {
                return (
                  <div
                    className={`_${timeFormatHandler(time)}  main-am`}
                    key={index}
                  >
                    <h2>{timeFormatHandler(time)}</h2>
                    <div className="items" id={time}></div>
                  </div>
                );
              } else {
                return (
                  <div
                    className={`_${timeFormatHandler(time)}  sub-am`}
                    key={index}
                  >
                    {timeFormatHandler(time)}
                    <div className="items-s" id={time}></div>
                  </div>
                );
              }
            })}
          </div>
        </div>
        <div className="pm">
          <div className="heading-pm">
            <h1>PM</h1>
          </div>
          <div className="time-divs-pm">
            {pm.map((time, index) => {
              if (!time.includes(".5")) {
                return (
                  <div
                    className={`_${timeFormatHandler(time)}  main-pm`}
                    key={index}
                  >
                    <h2>{timeFormatHandler(time)}</h2>
                    <div className="items" id={time}></div>
                  </div>
                );
              } else {
                return (
                  <div
                    className={`_${timeFormatHandler(time)}  sub-pm`}
                    key={index}
                  >
                    {timeFormatHandler(time)}
                    <div className="items-s" id={time}></div>
                  </div>
                );
              }
            })}
            <div className="main-pm"></div>
          </div>
        </div>
      </div>
    </div>
  );
}
