import * as React from "react";
import axios from "axios";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import "./modal.css";
import useFetch from "../../../Hooks/useFetch";
import EditIcon from "@mui/icons-material/Edit";
import { Autocomplete, TextField } from "@mui/material";
import { getCities } from "../../../Services/CityNames";
import { SINGLE_EVENT } from "../../../utils/Events/Constants/constants";

export default function Edit({ editId }) {
  // Get current User
  const user_email = localStorage.getItem("email");

  // city names api handlers
  const [cities, setCities] = React.useState([]);
  let temp_arr = [];

  // Drop down options handlers
  let options = [];
  for (let i = 9; i <= 20.5; i += 0.5) {
    options.push(i);
  }

  // Modal handlers
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  // New event handler
  const [name, setName] = React.useState("");
  const [location, setLocation] = React.useState("");
  const [allDay, setAllDay] = React.useState();
  const [start, setStart] = React.useState("");
  const [end, setEnd] = React.useState("");
  const [once, setOnce] = React.useState(true);
  const [errors, setErrors] = React.useState("");


  // Get data to edit
  const { data, error } = useFetch(SINGLE_EVENT(editId));

  const editEvent = () => {
    try {
      axios.put(SINGLE_EVENT(editId), {
          start,
          end,
          name,
          location,
          allDay,
          user_email,
      }).then((res) => {
        if (!res.ok) {
          setErrors("Could not save data. Make sure all fields are full");
        }
      });
    } catch (error) {
      console.log(error);
    }
  };

  // Disabling end time tha is smaller than start time
  const disable = (e) => {
    var id = "";
    document.querySelectorAll(".start").forEach((opt) => {
      if (opt.value === e.target.value) {
        id = opt.id;
        setStart((s) => id);
      }
    });
    document.querySelectorAll(".end").forEach((opt) => {
      if (parseFloat(opt.id) <= parseFloat(id)) {
        opt.disabled = true;
      }
    });
  };

  // For removing duplicates in the cities API
  function removeDuplicates(arr) {
    var unique = [];
    arr.forEach((element) => {
      if (!unique.includes(element)) {
        unique.push(element);
      }
    });
    return unique;
  }

  // Get all the cities of Pakistan from this API
  const getLocations = async () => {
    await getCities(temp_arr);
    setCities(removeDuplicates(temp_arr));
  };

  React.useEffect(() => {
    getLocations();
  }, []);

  React.useEffect(() => {
    if (data && once) {
      setOnce(false);
      setName((n) => data.name);
      setLocation((l) => data.location);
      setAllDay((a) => data.allDay);
      setStart((s) => data.start);
      setEnd((se) => data.end);
      getLocations();
    }
  }, [data]);

  return (
    <div>
      <button title="Edit this event" onClick={handleOpen} className="edit-btn">
        <EditIcon />
      </button>
      <Modal open={open} onClose={handleClose}>
        <Box className="modal-box">
          {data && (
            <form className="modal-form">
              <h1 className="all">Edit Event</h1>
              <label>Event Name</label>
              <input
                type="text"
                name="name"
                defaultValue={data.name}
                onChange={(e) => setName((n) => e.target.value)}
                required
              />
              <Autocomplete
                disablePortal
                id="combo-box-demo"
                options={cities && cities}
                sx={{ width: "100%", height: "50px" }}
                defaultValue={data.location}
                onChange={(e) => setLocation(e.target.textContent)}
                renderInput={(params) => (
                  <TextField {...params} label="Location" />
                )}
              />
              <label>Event Type:</label>
              <div className="radio">
                <input
                  className="radio-btn"
                  type="radio"
                  id="allDay"
                  name="event_type"
                  value={true}
                  onChange={(e) => {
                    console.log(e.target.value);
                    setAllDay((a) => e.target.value);
                  }}
                />
                All Day
                <input
                  className="radio-btn"
                  type="radio"
                  id="timely"
                  name="event_type"
                  value={false}
                  onChange={(e) => {
                    console.log(e.target.value);
                    setAllDay((a) => e.target.value);
                  }}
                />
                Timely
              </div>

              <div className="time-fields">
                <label>Start Time</label>
                <select
                  defaultValue={start}
                  required
                  onChange={(e) => {
                    setStart((s) => e.target.value);
                    disable(e);
                  }}
                >
                  {options &&
                    options.map(
                      (
                        opt // Renders a list of times in dropdown
                      ) => (
                        <option
                          className="start"
                          id={opt}
                          key={options.indexOf(opt)}
                          value={opt}
                        >
                          {opt}
                        </option>
                      )
                    )}
                </select>
                <label>End Time</label>
                <select
                  required
                  defaultValue={end}
                  onChange={(e) => {
                    setEnd((se) => e.target.value);
                  }}
                >
                  {options &&
                    options.map(
                      (
                        opt // Renders a list of times in dropdown
                      ) => (
                        <option
                          className="end"
                          id={opt}
                          key={options.indexOf(opt)}
                          value={opt}
                        >
                          {opt}
                        </option>
                      )
                    )}
                </select>
              </div>
              <div className="error">{errors}</div>
              <div className="btns">
                <button className="cancel" onClick={handleClose}>
                  Cancel
                </button>
                <button onClick={editEvent}>Save</button>
              </div>
            </form>
          )}
        </Box>
      </Modal>
    </div>
  );
}
  