// Add user's data to local

export const inLocal = (props) => {
  window.localStorage.setItem("token", props.token);
  window.localStorage.setItem("email", props.email);
  window.localStorage.setItem("firstName", props.fName);
  window.localStorage.setItem("lastName", props.lName);
};

// Get data from local
export const outLocal = (key) => {
  return window.localStorage.getItem(key);
};
